<template>
  <div class="post">
    <custom-header :heading="posts[0].title" :image="posts[0].image" ></custom-header>

    <!-- Post Content -->
    <article>
      <div class="container">
        <div class="row">
          <div class="col-lg-8 col-md-9 mx-auto">

            <!-- Displays post -->
            <post-body :body="posts[0].body"></post-body>

          </div>
          <!-- <div class="col-lg-4 col-md-3 mx-auto">
            <div class="col-md-12"></div>
          </div> -->
        </div>
      </div>
    </article>
  </div>
</template>

<script>
// Import Libraries
const axios = require("axios");

// Import Components
import PostBody from "@/components/post/Body.vue";

function buildUrl(blog, slug) {
  return "http://localhost:8000/api/blog/" + blog + "/" + slug;
}

export default {
  components: {
    "post-body": PostBody
  },
  data() {
    return {
      posts: []
    };
  },
  created() {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData();
  },
  watch: {
    // call again the method if the route changes
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      let url = buildUrl(this.$route.params.blog, this.$route.params.slug);
      axios
        .get(url)
        .then(response => {
          this.posts = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }

};
</script>
